body {
  background-color: #00002a;
  margin: 0;
  padding: 0;
  overflow: hidden;
}
#loader {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 140px;
  transform: translate(-50%, -50%);
}
#loader .pair {
  position: absolute;
  width: 60px;
  height: 10px;
}
#loader .pair .dot {
  position: absolute;
  width: 10px;
  height: 10px;
  background-color: #fefcf6;
  border-radius: 50%;
}
#loader .pair .dot-2 {
  right: 0;
}
#loader .pair-2 {
  left: 50px;
}
#loader .pair-1 {
  animation: move 1000ms ease-in-out infinite;
}
#loader .pair-2 {
  animation: move 1000ms ease-in-out infinite reverse;
}
@keyframes move {
  0% {
    transform: rotate(0deg);
  }
  50%,
  100% {
    transform: rotate(180deg);
  }
}
#loader .pair-1 .dot-1 {
  animation: hide 1000ms ease-in-out infinite reverse;
  opacity: 0;
}
#loader .pair-2 .dot-2 {
  animation: hide 1000ms ease-in-out infinite;
  opacity: 0;
}
@keyframes hide {
  0%,
  49% {
    opacity: 0;
  }
  50%,
  100% {
    opacity: 1;
  }
}
.text {
  position: absolute;
  margin-top: 60%;
  left: 50%;
  font-size: 50px;
  transform: translate(-50%, -50%);
  color: #fefcf6;
  font-family: courier new;
}
#android {
  position: absolute;
  top: 200px;
  left: 40%;
  transform: translate(-50%, -50%);
  font-size: 20px;
  color: #43a047;
}
#apple {
  position: absolute;
  top: 200px;
  left: 60%;
  transform: translate(-50%, -50%);
  font-size: 20px;
  color: #cfd8dc;
}
#copyright {
  position: absolute;
  top: 230px;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 10px;
  font-family: courier new;
  color: #cfd8dc;
}
#loader {
  display: none;
}
