.header {
  display: flex;
  width: 100%;
  height: 7vh;
  align-items: center;
  justify-content: space-around;
  color: white;
  margin-top: 5vh;
  font-size: 15px;
  font-family: "Montserrat", sans-serif;
}

.song-details {
  width: 100%;
  height: 40vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.act-img {
  width: 260px;
  height: 260px;
  border: 1px solid #154360;
  box-shadow: 0px 0px 10px 1px black;
  border-radius: 50%;
}
#loadbar,
#song-name,
#artist-name {
  width: 100%;
  height: 10vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 22px;
  color: white;
  font-family: courier new;
}
#song-name {
  font-weight: bolder;
  position: absolute;
  top: 60%;
  margin-bottom: 5px;
  font-size: 35px;
}
#artist-name {
  position: absolute;
  top: 65%;
  color: #fefcf6;
  font-size: 20px;
}
#loadbar {
  position: absolute;
  top: 70%;
  font-size: 20px;
  color: red;
}
.button-control {
  width: 100%;
  position: absolute;
  height: 20vh;
  display: flex;
  top: 90%;
  left: 50%;
  transform: translate(-50%, -50%);
  justify-content: space-around;
  align-items: center;
}
#prev {
  position: absolute;
  font-size: 40px;
  color: #fefcf6;
  margin-left: -20%;
  cursor: pointer;
}
#next {
  position: absolute;
  font-size: 40px;
  color: #fefcf6;
  margin-left: 20%;
  cursor: pointer;
}
#fav {
  position: absolute;
  font-size: 30px;
  margin-left: 40%;
  cursor: pointer;
}
#play {
  position: absolute;
  font-size: 40px;
  color: #fefcf6;
  margin-left: 0%;
  cursor: pointer;
}
#shuffle {
  position: absolute;
  font-size: 30px;
  color: #fefcf6;
  margin-left: -40%;
}
.bg-img {
  position: absolute;
  top: 0;
  left: 0;
  height: auto;
  width: 100%;
  z-index: -10;
}
.song-pic {
  position: absolute;
  top: 20%;
}

/*SIDEBAR*/
#Sidebar {
  position: absolute;
  width: 200px;
  height: 100%;
  background-color: #00002a;
  left: -200px;
  transition: all 500ms linear;
  top: 2%;
  font-family: courier new;
  display: flex;
}

#Sidebar.active {
  left: 0px;
}

#Sidebar .Tg_Button {
  position: absolute;
  left: 210px;
  top: 10px;
  background-color: none;
  cursor: pointer;
}

#Sidebar .Tg_Button span {
  display: block;
  color: #fefcf6;
  background-color: transparent;
  border-radius: 50%;
}
#Sidebar ol li {
  color: #fefcf6;
  list-style: none;
  padding: 15px 10px;
  border-bottom: 1px solid rgba(100, 100, 100, 0.3);
  cursor: pointer;
}
#Sidebar ol {
  overflow-y: scroll;
}

#about {
  position: absolute;
  top: 3%;
  left: 93%;
  color: #fefcf6;
  cursor: pointer;
  font-size: 25px;
}
#Sidebar ol::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #00002a;
}

#Sidebar ol::-webkit-scrollbar {
  width: 10px;
  background-color: #00002a;
}

#Sidebar ol::-webkit-scrollbar-thumb {
  background-color: #00002a;
}
.activeitem {
  font-weight: 900;
}

.progress {
  width: 100%;
  max-width: 40%;
  height: 6px;
  background: #e1e4e8;
  overflow: hidden;
}
.progress-bar {
  display: block;
  height: 100%;
  background: linear-gradient(
    90deg,
    #ffd33d,
    #ea4aaa 17%,
    #b34bff 34%,
    #01feff 51%,
    #ffd33d 68%,
    #ea4aaa 85%,
    #b34bff
  );
  background-size: 300% 100%;
  animation: progress-animation 2s linear infinite;
}
@keyframes progress-animation {
  0% {
    background-position: 100%;
  }
  100% {
    background-position: 0;
  }
}
